import React, {useState} from 'react';
import {Card, Button, Row, Col, Avatar, Divider, Typography} from 'antd';
import {ButtonLink, Drawer, PageTitle, ReactTable, NumberFormat, Modal} from '../../units';
import { UserOutlined, UnorderedListOutlined } from '@ant-design/icons';
import {useOneUser, userOneUser} from '../../hooks';
import {formatDate} from '../../helpers';
import {MailOutlined, PhoneOutlined, WalletOutlined, UsergroupAddOutlined} from '@ant-design/icons'
import TopUp from "./modals/TopUp";

const {Text, Title} = Typography
export default function UserDetail(props) {
    let userId = props.match.params.id;
    const [showTopUp, setShowTopUp] = useState(false);
    const [refreshVariable, setRefreshVariable] = useState(0)
    const { status, data, error, isFetching } = useOneUser(userId, refreshVariable);
    let contactGroups = data? data.data.contactGroups: [];
    let userDetail = data? data.data.userDetail: {};
    let balance = data? data.data.walletBalance: {};
    let contacts = data? data.data.contacts: 0;

    return (
        <Drawer selectedKey="2">

            {/* =============== START TOP UP MODAL =======*/}
            <Modal 
 visible={showTopUp}
 title="Top Up Wallet"
//  onOk={setNextStageModal}
 onCancel={setShowTopUp}
 formId="topUpForm"
 >
<TopUp user={userId} currentBalance={balance.amount} 
closeModal={setShowTopUp}
refreshVariable={setRefreshVariable}
/>
</Modal>
            {/* =============== END TOP UP MODAL =======*/}
            <Card>
            <Row>
                <Col xs={8} md={2}>
                <Avatar shape="square" size={64} icon={<UserOutlined />} />
                </Col>
                <Col xs={14} md={20}>
                    <Title level={5}>{userDetail.name}</Title>
                    <MailOutlined /> : <Text>{userDetail.email}</Text>
                    &nbsp; &nbsp; &nbsp; &nbsp;<PhoneOutlined />: <Text>{userDetail.phoneNumber}</Text>
                </Col>
                <Col xs={2} md={2}>
                    <Button type="primary" onClick={()=> setShowTopUp(!showTopUp)}>Top Up</Button>
                </Col>
                <Divider />
    <StatCards label="Contacts" value={contacts} icon={<UnorderedListOutlined style={{height: 'inherit'}}/>}/>
    <StatCards label="Balance" value={<NumberFormat value={balance.amount} />} icon={<WalletOutlined style={{height: 'inherit'}} />} />
    <StatCards label=" Groups" value={contactGroups.length}  icon={<UsergroupAddOutlined style={{height: 'inherit'}} />}/>

            </Row>
            </Card>

            <Card style={{marginTop: '20px'}}>
                <UserGroups  contactGroups={contactGroups}/>
            </Card>
        </Drawer>
    )
}


const StatCards = ({label, value, icon}) => {
    return (
        <Col xs={8} sm={8} md={4} style={{borderRight: '1px solid #EDEDED'}}>
        <Row gutter={2}>
            <Col span={8} style={{marginLeft: '5px'}}>
        <Title level={1} >{icon}</Title>
        </Col>
        <Col span={14}>
        <Title level={5} style={{marginBottom: '0px'}} >{label}</Title>
        <Title level={5} style={{marginTop: '0px'}}>{value}</Title>
        </Col>
        </Row>
    </Col>
    )
}

const UserGroups = ({contactGroups}) => {
    return (
        
        <Row>
            <PageTitle title="Contact Groups" />
        <Col span={24}>
            <Row>
                <Col span={4}>
                   <strong> Date </strong>
                </Col>

                <Col span={8}>
                   <strong> Name</strong>
                </Col>
                <Col span={4}>
                   <strong> Members</strong>
                </Col>
                <Col span={4}>
                   <strong> Status</strong>
                </Col>
                <Col span={4}>
                   <strong> Action</strong>
                </Col>
            </Row>

            {contactGroups.length === 0? (<center style={{marginTop: "20px"}}>No Group Found</center>) : (
            <>
            {
                contactGroups.map(contactGroup => (
                         <Row>
                   <Col span={4}>
                       {formatDate(contactGroup.updatedAt)}
                   </Col>
   
                   <Col span={8}>
                       {contactGroup.name}
                   </Col>
                   <Col span={4}>
                       {contactGroup.contacts.length}
                   </Col>
                   <Col span={4}>
                       {contactGroup.contacts.status}
                   </Col>
                   <Col span={4}>
                       <ButtonLink size="small" type="primary" to="#" label="View" />
                   </Col>
               </Row>
                ))
            }
            </>
            )}
        </Col>
        </Row>
    )
}